.container{
     width: 100%;
     position: relative;
     margin: 6em 0;
}
.card.MuiCard-root.MuiPaper-root {
     font-size: 18px;
     padding: 1em;
     background-color: transparent;
     color: #dbdbdb;
     box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.38), -6px -6px 8px rgba(255, 255, 255, 0.02);
     border-radius: 12px;
}
/* time */
.card .MuiTypography-colorTextSecondary {
     color: inherit;
     margin: 6px 0;
}

/* tags */
.card .MuiTypography-root.card__title {
     font-size: 1em;
     display: inline-block;
     background-color: #4a8b9e;
     color: #f2f2f2;
     padding: 9px 1em;
     margin-bottom: 1em;
     border-radius: 6px;
     box-shadow: inset -5px -5px 10px rgba(255, 255, 255, 0.05), inset 5px 5px 15px rgba(0, 0, 0, 0.5);
     background-image:linear-gradient(135deg, rgba(0,0,0,0.22), rgba(255,255,255,0.25));
}
.MuiTypography-root.card__title.card__recovered {
     background-color: #559e4a;
}
.MuiTypography-root.card__title.card__death{
     background-color: #9e4a4a;
}
/* gap */
.card.MuiCard-root.MuiPaper-root:nth-child(2){
     margin: 0 4rem;
}
.MuiCardContent-root:last-child{
     padding-bottom: 0 !important;
}

/* card titile */
.MuiTypography-h5{
     font-weight: bolder !important;
     letter-spacing: 1px !important;
     font-size: 2.5em !important;
}
.MuiCardContent-root:last-child {
     padding-bottom: 14px;
}

@media (max-width:840px){
     .card.MuiCard-root.MuiPaper-root:nth-child(2){
          margin: 0rem;
     }
}

@media (max-width:600px){
     .container {
          margin: 3em 0 !important;
     }
     .card.MuiCard-root.MuiPaper-root{
          font-size: 13px;
     }
     .MuiTypography-h5 {
          letter-spacing: 0px !important;
          font-size: 22px !important;
     }
     .MuiCardContent-root{
          padding: 0 !important  ;
     }
     .card .MuiTypography-root.card__title{
          padding: 9px 1em;
     }
     .MuiTypography-body2{
          display: none;
     }
     .card .MuiTypography-colorTextSecondary{
          font-size: 12px;;
     }
     .MuiGrid-spacing-xs-3{
          overflow: hidden;
     }
     .card.MuiCard-root.MuiPaper-root{
          border-radius: 0;
     }
}

@media (max-width:350px){
     .card.MuiCard-root.MuiPaper-root{
          padding: 6px;
     }
     .MuiTypography-h5{
          font-size: 20px !important;
     }
     .card .MuiTypography-root.card__title {
          padding: 5px 12px;
          text-align: center;
     }
}



.contaier{
     margin-bottom: 2em;
}
.chartjs-render-monitor{
     box-shadow: rgba(0, 0, 0, 0.38) 8px 8px 8px, rgba(255, 255, 255, 0.02) -6px -6px 8px;
     transform: scale(0.9);
     padding: 2em;
     transform-origin: left top 0px;
     border-radius: 12px;
}

@media (max-width:600px){
     .chartjs-render-monitor{
          padding: 1em;
     }
}
.material-icons{
    position: absolute;
    right: 1rem;
    color: #303030;
    top: 4rem;
    pointer-events: none;
}
.MuiNativeSelect-icon{
    display: none !important;
}

.form-group{
    margin: 3rem auto;
    display: flex;
    justify-content: flex-start;
}
.form-control {
    width: 100%;
    margin: 1rem auto;
    display: flex;
    justify-content: flex-start;
}
.MuiFormControl-root {
    width: 70%;
}
#demo-simple-select {
    background-color: #fff;
    height: 44px;
    padding: 6px;
    color: #303030;
    border-bottom: 0;
    border-radius: 10px;
    width: 100%;
    padding: 8px 1em;
    box-shadow: -5px -5px 10px rgba(255, 255, 255, 0.05), 5px 5px 15px rgba(0, 0, 0, 0.5), inset -5px -5px 10px rgba(255, 255, 255, 0.05), inset 5px 5px 15px rgba(0, 0, 0, 0.5);
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    /* no standardized syntax available, no ie-friendly solution available */
}


.MuiFormLabel-root.MuiInputLabel-root.MuiInputLabel-formControl {
    color: #fff;
    font-size: 1.6em;
    transform: none;
}
.MuiFormControl-root label + .MuiInput-formControl {
    margin-top: 3em;
}

.covid--header {
    background-image: linear-gradient(250deg, #6c9ba8 80%, #6aa161 90%, #a56565 66%);
    -webkit-background-clip: text;
            background-clip: text;
    color: transparent;
}


@media (max-width:600px){
    #demo-simple-select{
        height: 34px;
    }
    .MuiFormLabel-root.MuiInputLabel-root.MuiInputLabel-formControl{
        font-size: 1em;
    }
    .MuiFormControl-root label + .MuiInput-formControl{
        margin-top: 2em;
    }
    #demo-simple-select{
        padding: 8px;
    }
    .material-icons{
        right: 6px;
        top:1.9em;
    }
    .form-group{
        margin-top: 10px;
    }
    .covid--header {
        background-image: linear-gradient(250deg, #6c9ba8 30%, #6aa161 60%, #a56565 92%);
    }
}
